.initialized{
	overflow: hidden;
}
.to_login{
	color: red;
	cursor: pointer;
}
.to_login:hover{
	text-decoration: underline;
}
.index_content_list li{
	height: 40px;
	line-height: 40px;
	position: relative;
	border-bottom:dashed 1px blue;
	cursor: pointer;
}
.index_content_list li:hover{
	background: rgb(98, 170, 229);
}
.index_time{
	font-style: normal;
	color: #666;
	font-size: 12px;
	margin-left: 15px;
	padding-bottom: 5px;
}
.index_words{
	display: inline-block;
	font-style: normal;
	float: left;
	width: 850px;
	height: 40px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	padding-left: 50px;
	color: #111;
}

*{
	margin:0;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	font-family: 'microsoft yahei';
}
a:hover,a:active,a:link,a:visited{
	text-decoration: none;
}
li{
	list-style: none;
}
.initialized{
	background: url(./static/img/bg.f865a11.jpg) no-repeat #f6f6f6 top center;
}
.container{
	width: 100%;
}
.container>h4{
	color: #333;
	padding-left: 60px;
}
/*导航栏*/
.nav{
	height: 50px;	
	margin-top: 80px;
	background: url(data:image/jpeg;base64,) repeat-x left center;
}
.nav ul{
	float: right;
	margin: 0;
}
.active{
	background: url(data:image/jpeg;base64,) no-repeat top center;
}
.nav ul li{
	float: left;
	text-align: center;
	cursor: pointer;
}
.nav ul li:hover{
	background: url(data:image/jpeg;base64,) no-repeat top center;
}
.nav ul li a{
	color: #fff;
	display: inline-block;
	width: 110px;
	line-height: 50px;
	font-size: 16px;
	padding: 0 15px;
}
.nav ul li:hover a{
	color: #fff;
}
/*用户信息栏*/
.userinfo{
	width: 21%;
	margin-top: 10px;
	height: 370px;
	border: solid rgb(22, 113, 180) 1px;
	padding: 0;
	border-radius: 6px;
	float: left;
	-webkit-box-shadow: 0px 0px  5px rgb(22, 113, 180);
	        box-shadow: 0px 0px  5px rgb(22, 113, 180);
}
.icon-calendar{
	margin-right: 6px;
}
.userinfo p,.content-list p,.permis-dialog p{
	background: rgb(22, 113, 180);
	height: 35px;
	line-height: 60px;
	position: relative;
}
.userinfo span,.content-list span,.permis-dialog span{
	height: 30px;
	font-size: 18px;
	line-height: 5px;
	background: rgb(225, 231, 242);
	bottom:0px;
	padding: 10px 15px;
	position: absolute;
	left: 50%;
	margin-left: -65px;
	border-radius: 6px 6px 0 0;
}
.userinfo-list{
	padding: 20px 0 0 35px;
}
/*消息列表页*/
.content-list{
	float: right;
	margin-top:10px;
	height: 370px;
	width: 78%;
	border-radius: 6px;
	border: solid rgb(22, 113, 180) 1px;
	-webkit-box-shadow: 0px 0px  5px rgb(22, 113, 180);
	        box-shadow: 0px 0px  5px rgb(22, 113, 180);
}
.content-list span{
	left:0;
	margin-left:60px;
}
/*底部版权页*/
.footer{
	width: 100%;
	height: 60px;
	border: solid rgb(22, 113, 180) 1px;
	border-radius: 6px 6px 0 0;
	position: fixed;
	bottom: 1px;
	border-bottom: none;
}
.footer p{
	line-height: 60px;
	text-align: center;
}
/*list页*/
.cur-location{
	width: 100%;
	height: 35px;
	line-height: 35px;
	border:solid rgb(22, 113, 180) 1px;
	margin:10px 0;
	border-radius: 3px;
	padding-left: 50px;
}
.cur-location a{
	-webkit-transition:0.2s ease-in-out;
	transition:0.2s ease-in-out;
}
.cur-location a:hover{
	color: red;
}
.content-list-item,.publishMes{
	width: 100%;
	height: 322px;
	border:solid rgb(22, 113, 180) 1px;
	border-radius: 3px;
	position: relative;
}
.content-list-item>ul>li{
	height: 40px;
	line-height: 20px;
	position: relative;
	border-bottom:dashed 1px blue;
	padding-left: 30px;
	cursor: pointer;
}
.content-list-item>ul>li:hover{
	background: rgb(98, 170, 229);
}
.publish-info{
	position: absolute;
	right: 5px;
	bottom: 0;
	font-size: 13px;
	color: #666;
}
.page-tab ul {
	position: absolute;
	width: 485px;
	bottom:0;
	left:0;
	right: 0;
	margin: auto;
	overflow: hidden;
}
.page-tab li a{
	display: inline-block;
	padding: 5px 10px;
	float: left;
	line-height:30px;
	height:40px;
	background: rgb(42, 129, 200);
	color: #fff;
	margin:0 5px;
	border-radius: 3px;
}
.page-tab li a:hover{
	background: rgb(0, 106, 161);
}
/*permission页*/
.permis-dialog{
	width: 350px;
	height: 200px;
	border:solid rgb(22, 113, 180) 1px;
	margin:50px auto;
	border-radius: 3px;
}
.ygbh input,.xtkl input{
	width: 170px;
	height: 30px;
	text-indent: 0.6em;
}
.ygbh,.xtkl{
	width: 250px;
	margin:25px auto 0;
}
.btn-group{
	margin: 12px 0 0 48px;
}
.btn-group button{
	width: 60px;
	height: 30px;
	line-height: 30px;
	cursor: pointer;
	background: rgb(22, 123, 180);
	border: none;
	outline: none;
	margin-right: 5px;
	color: #fff;
	border-radius: 3px;
}
.btn-group button:hover{
	background: rgb(0, 106, 161);
}
/*pubilishMes页*/
.publishMes{
	padding-left:120px; 
	position: relative;
}
.notice{
	color: red;
}
.mesTitle{
	width: 730px;
	text-indent: 0.6em;
}
/*组件CSS*/
/*login组件*/
#home .loginbox {
    position: fixed;
    z-index:2000;
    width: 100%;
    height: 100%;
    left: 0;
    top:0;
    background: rgba(0, 0, 0, .85);
}
#home .loginbox .box {
    border-radius: 5px;
    height: 300px;
    width: 300px;
    padding: 40px 20px 20px;
    margin: 100px auto;
    background: #fff;
}
#home .loginbox .box h3 {
    font-size: 20px;
    font-family: "\5FAE\8F6F\96C5\9ED1";
    text-align: center;
}
#home .loginbox .box input {
    height: 40px;
    width: 100%;
    border: 1px solid red;
    margin: 15px 0;
    border-radius: 2px;
    text-indent: 0.6em;
    border-radius: 5px;
    outline: none;
	font-size: 14px;
	color: #333;
}
#home .loginbox .box input[type="button"] {
    background: red;
    color: #fff;
    font-family: "\5FAE\8F6F\96C5\9ED1";
    margin-top: 20px;
    cursor: pointer;
    border:none;
    border-radius: 5px;
}
#home .loginbox .box input[type="button"]:hover{
	background: rgb(22, 123, 180);
}
/*welcome-info*/
.welcom_info,.cur_user_info{
	position: absolute;
	right: 15px;
	top:25px;
}
.quit_btn{
	border:none;
	outline: none;
	background: transparent;
	cursor: pointer;
	color: #fff;
	-webkit-transition:0.6s;
	transition:0.6s;
	font-size: 15px;
